import { Group, ObjectLoader } from "three";
import MODEL from "./scene.json";
import * as TWEEN from "@tweenjs/tween.js";
import State from "../../state";

export default class Land extends Group {
  constructor() {
    const loader = new ObjectLoader();

    super();

    this.name = "land";
    State.listener = this;

    loader.load(MODEL, (mesh) => {
      this.add(mesh);

      this._dogHead = this.getObjectByName("doggie-head-center.glb");
      this._dogHead.rotation.y = -1;

      this._ground = this.getObjectByName("Ground");
      this._ground.position.y = -0.1;
      this._ground.scale.x = 1.2;
      this._ground.scale.y = 4;
      this._ground.scale.z = 1.2;


      document.addEventListener("keydown", (e) => {
        if (true) {
          State.pushEvent();
        }
      });
    });
  }

  onEvent() {
    new TWEEN.Tween(this._dogHead.rotation)
    .to({ y: 0.7, x: -0.5 }, 2000)
    .delay(500)
    .easing(TWEEN.Easing.Back.InOut)
    .yoyo(true)
    .repeat(1)
    .start();
  }
}
